import React from 'react';
import '../styles/Blog.css';

const Blog = () => {
  const blogPosts = [
    {
      id: 1,
      title: 'Understanding React Hooks: A Comprehensive Guide',
      date: 'April 14, 2024',
      excerpt: 'Deep dive into React Hooks and how they revolutionize state management in functional components. Learn about useState, useEffect, and custom hooks.',
      category: 'React Development',
      image: '/images/react-hooks.jpg',
      readTime: '5 min read',
      slug: 'understanding-react-hooks'
    },
    {
      id: 2,
      title: 'Stock Market Analysis: Technical vs Fundamental',
      date: 'April 12, 2024',
      excerpt: 'Comparing technical and fundamental analysis approaches in stock market trading. Learn which method suits your investment strategy.',
      category: 'Stock Market',
      image: '/images/stock-analysis.jpg',
      readTime: '7 min read',
      slug: 'stock-market-analysis-comparison'
    },
    {
      id: 3,
      title: 'Digital Marketing Strategies for 2024',
      date: 'April 10, 2024',
      excerpt: 'Explore the latest digital marketing trends and strategies that are shaping the industry in 2024. From SEO to social media marketing.',
      category: 'Digital Marketing',
      image: '/images/digital-marketing.jpg',
      readTime: '6 min read',
      slug: 'digital-marketing-strategies-2024'
    }
  ];

  return (
    <div className="blog-container">
      <h1>Blog</h1>
      <p className="blog-intro">
        Insights and articles about React Development, Stock Market Analysis, and Digital Marketing
      </p>
      <div className="blog-grid">
        {blogPosts.map(post => (
          <article key={post.id} className="blog-card">
            <div className="blog-image">
              <img src={post.image} alt={post.title} />
              <span className="category-tag">{post.category}</span>
            </div>
            <div className="blog-content">
              <div className="blog-meta">
                <span className="date">{post.date}</span>
                <span className="read-time">{post.readTime}</span>
              </div>
              <h2>{post.title}</h2>
              <p>{post.excerpt}</p>
              <a href={`/blog/${post.slug}`} className="read-more">
                Read More <i className="fas fa-arrow-right"></i>
              </a>
            </div>
          </article>
        ))}
      </div>
    </div>
  );
};

export default Blog; 